<h3 id="hard" tabindex="-1"><a class="header-anchor" href="#hard" aria-hidden="true">#</a> hard</h3>
<p>• <code>Optional</code> <strong>hard</strong>: <code>boolean</code></p>
<p>By default the wrap is soft, meaning long words may extend past the column width. Setting this to true will make it hard wrap at the column width.
default: false</p>
