<h3 id="trim" tabindex="-1"><a class="header-anchor" href="#trim" aria-hidden="true">#</a> trim</h3>
<p>• <code>Optional</code> <strong>trim</strong>: <code>boolean</code></p>
<p>Whitespace on all lines is removed by default. Set this option to false if you don't want to trim.
default: true</p>
<h4 id="defined-in-1" tabindex="-1"><a class="header-anchor" href="#defined-in-1" aria-hidden="true">#</a> Defined in</h4>
