<h3 id="wordwrap" tabindex="-1"><a class="header-anchor" href="#wordwrap" aria-hidden="true">#</a> wordWrap</h3>
<p>• <code>Optional</code> <strong>wordWrap</strong>: <code>boolean</code></p>
<p>By default, an attempt is made to split words at spaces, ensuring that they don't extend past the configured columns.
If wordWrap is false, each column will instead be completely filled splitting words as necessary.
default: true</p>
